.bluebar {
  background-color: #00a7f4;
  height:14px;
}

.errorprompt {
  color:red;
}

button {
  margin: 0.4em;
  padding: 0.8em;
  background-color:#00a7f4;
  color:white;
  border: 0px solid white;
  border-radius: 4px;
  font-size: 85%;
}

button:disabled {
  background-color:#aec7d3;
}


/***  InputView:  ***/

.inputview {
  padding: 15px;
  padding-left:30px;

  border: 1px solid #EEE;
}

.inputview .numberinputs{
  width:100%;
  padding-top: 20px;
  padding-bottom:30px;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(18, auto);
}

.inputview .numberinputs input {
  margin-bottom: 4px; /* top/bottom */
  text-align: center;
  padding: 6px 4px 6px 4px;
  width:40px;
  width:3em;
  font-size: 100%;
  margin-left:0px;
}

.inputview .numberinputs .label{
  float:left;
  min-width:2.5em;
  min-height:1em;
  padding-top:5px;
}

.inputview .numberinputs .submittable{
  border: 1px solid green;
}

.inputview .numberinputs .notsubmittable{
  border: 1px solid lightgray;
}

.inputview .numberinputs .disabled{
  border: 1px solid rgba(255, 255, 255, 0); 
  background-color: rgb(238, 238, 238);
  pointer-events:none;
}

.inputview .age{
  grid-column: 1/4;
  grid-row: 2/3;
}

.inputview .age input{
  width: 3em;
  padding: 6px 4px 6px 4px;
  margin-left:1em;
  text-align: center;
}

.inputview .resetbutton{
  float:right;
  margin-right: calc(33% - 7.5em);
}


/***  ResultsView:  ***/

.resultsView{
  padding-left:30px;
}
.resultsView .printbutton{
  margin-top: 1em;
}

.resultsView img{
  max-width:60vw;
  max-width:800px;
}

.kokpisteet {
  transform: rotate(-90deg) translate(-230px, -165px);
}

.linestyle {
  stroke-width: 1px;
}

@media print { 
  /* All your print styles go here */
  button { display: none !important; } 
  .resultsView img { width:85vw; }
  .chart svg { width:85vw; }
  .resultsView{ padding:0px; margin: 0px}
 }